import Axios from "axios";
import React, { useState, useEffect } from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
const ReactMarkdown = require('react-markdown');
export default function HomePage() {
  const [markdownList, setMarkdownList] = useState([]);
  const [markdownData, setMarkdownData] = useState("");
  const [clickName, setClickName] = useState("");
  const dest_url =
    (process.env.REACT_APP_IP ?? "127.0.0.1") +
    ":" +
    (process.env.REACT_APP_PORT ?? "538");
  const [c] = useState(0);
  useEffect(() => {
    Axios.get(`http://${dest_url}/markdown_list`).then((data) => {
      setMarkdownList(
        data.data.data.map((item) => {
          return {
            name: item.name,
          };
        })
      );
    });
  }, [c]);
  useEffect(() => {
      console.log(clickName);
    Axios.get(`http://${dest_url}/markdown?name=${clickName}`).then((data) => {
      setMarkdownData(data.data.data[0].content);
    });
  }, [clickName, c]);

  return (
    <Container>
      <Row>
        <Col xs={2}>
          {markdownList
            .sort((a, b) => {
              return a.name.localeCompare(b.name);
            })
            .map((item) => {
              return (
                  <>
                <a
                  href="#"
                  onClick={() => {
                    setClickName(item.name);
                  }}
                  key={item.name}
                >
                  {item.name}
                </a>
                <br/>
                </>
              );
            })}
        </Col>
        <Col><ReactMarkdown source={markdownData} /></Col>
      </Row>
    </Container>
  );
}
