import React, { useState, useEffect } from "react";
import Axios from "axios";
import BootstrapTable from "react-bootstrap-table-next";
import NewsCard from "./NewsCard";
import Container from 'react-bootstrap/Container';
import Button from 'react-bootstrap/Button';
import AddSourceModal from './AddSourceModal';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
export default function NewsPage() {

  const [dailyRead, setDailyRead] = useState([]);
  const [readTabs, setReadTabs] = useState([]);
  const [readThings, setReadThings] = useState([]);
 
  const [c] = useState(0);
  const dest_url = (process.env.REACT_APP_IP??"127.0.0.1")+":"+(process.env.REACT_APP_PORT??"538");
  useEffect(() => {
    Axios.get(
      `http://${dest_url}/general_news`
    ).then((data) => {
        setReadThings(
        data.data.data.map((item, index) => {
          return item.name;
        })
      );
    });
  }, [c]);
  useEffect(() => {
    const get_list =readThings.map((item) => {
        return Axios.get(
          `http://${dest_url}/rss?name=${item}`
        );
      });
      Promise.all(get_list)
      .then((values) => {
          setReadTabs(
          values.map((item,index)=>{
            var tmp_rs = item.data.data.map((titem, index) => {
                return {name:titem.name,url:titem.url};
              });
            return {name:readThings[index],rs:tmp_rs};
          }));}
      );
  }, [c,readThings]);
  
  useEffect(() => {
    const get_list =readThings.map((item) => {
        return Axios.get(
          `http://${dest_url}/rss?name=${item}`
        );
      });
      Promise.all(get_list)
      .then((values) => {
          setReadTabs(
          values.map((item,index)=>{
            var tmp_rs = item.data.data.map((titem, index) => {
                return {name:titem.name,url:titem.url};
              });
            return {name:readThings[index],rs:tmp_rs};
          }));}
      );
  }, [c,readThings]);
  useEffect(() => {
    Axios.get(`http://${dest_url}/daily_read`).then((data) => {
        setDailyRead(
        data.data.data.map((item) => {
          return {
            name: item.name,
            url: item.url,
            up: item.up,
            published: item.published,
          };
        })
      );
    });
  }, [c]);
  const columns = [
    {
      dataField: "name",
      text: "Name",
    formatter:(cell,row)=>{return <a href={row.url} target="_blank" rel="noopener noreferrer">{row.name}</a>}
    },
    {
      dataField: "published",
      text: "DATE",
      formatter:(cell,row)=>{const time = new Date(row.published*1000);return time.toDateString();}
    }
  ];
  return <>
  
   <AddSourceModal title="new general news" source="general_news"/>
      
  <Container>
  <Row>
      {readTabs.map((tab) => {return <Col key={tab.name}><NewsCard data={tab.rs} name={tab.name}/>
  </Col>;})}
    </Row>
  <Row>
    <Col>
    <AddSourceModal title="new daily news" source="daily_news"/>
    <BootstrapTable keyField="id" data={dailyRead.sort((a,b)=>{return b.published - a.published})} columns={columns} /></Col>
    
  </Row>
</Container>
  
  </>;
}
