import React, { useState, useEffect } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import Axios from "axios";
import AddSourceModal from './AddSourceModal';
export default function VideoPage() {
  const [video, setVideo] = useState([]);
  const dest_url =
    (process.env.REACT_APP_IP ?? "127.0.0.1") +
    ":" +
    (process.env.REACT_APP_PORT ?? "538");
  const [c] = useState(0);
  useEffect(() => {
    Axios.get(`http://${dest_url}/video`).then((data) => {
      setVideo(
        data.data.data.map((item) => {
          return {
            name: item.name,
            url: item.url,
            up: item.up,
            published: item.published,
          };
        })
      );
    });
  }, [c]);
  const columns = [
    {
      dataField: "up",
      text: "Up",
    },
    {
      dataField: "name",
      text: "Name",
    formatter:(cell,row)=>{return <a href={row.url} target="_blank" rel="noopener noreferrer">{row.name}</a>}
    },
    {
        dataField: "published",
        text: "Date",
      formatter:(cell,row)=>{const time = new Date(row.published*1000);return time.toDateString();}
      },
  ];
  return <> <AddSourceModal title="new bilibili" source="video"/>
  
  <BootstrapTable keyField="published" data={video.sort((a,b)=>{return b.published-a.published})} columns={columns} /></>;
}
